'use client';

import { useState, useEffect } from 'react';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { db, auth } from '@/src/firebaseConfig';
import {
  collection,
  addDoc,
  doc,
  updateDoc,
  serverTimestamp,
} from 'firebase/firestore';
import { useGeocodeAddress } from '@/src/hooks/useGeocodeAddress';
import { BusinessType } from '@/src/lib/my-business/businessTypes';
import { useRouter } from 'next/navigation';

interface Props {
  businessType: BusinessType;
}

export default function CreateBusinessForm({ businessType }: Props) {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [displayAddress, setDisplayAddress] = useState('');
  const [mapAddress, setMapAddress] = useState('');
  const [location, setLocation] = useState('');
  const [loading, setLoading] = useState(false);

  const { geocode, coords, error: geoError, loading: geoLoading } = useGeocodeAddress();
  const router = useRouter();

  useEffect(() => {
    if (coords) {
      setLocation(`${coords.lat},${coords.lng}`);
    }
  }, [coords]);

  const handleBlur = () => {
    if (mapAddress.trim()) geocode(mapAddress);
  };

  const handleSubmit = async () => {
    const user = auth.currentUser;
    if (!user) return alert('You must be logged in.');

    if (!name || !email || !phone || !displayAddress || !mapAddress || !location) {
      return alert('Please fill in all fields.');
    }

    const [latStr, lngStr] = location.split(',').map((s) => s.trim());
    const lat = parseFloat(latStr);
    const lng = parseFloat(lngStr);
    if (isNaN(lat) || isNaN(lng)) {
      return alert('Invalid coordinates.');
    }

    const formattedLocation = `${lat}¬∞ N, ${lng}¬∞ E`;
    setLoading(true);

    try {
      const data = {
        name,
        email,
        phone,
        displayAddress,
        mapAddress,
        location: formattedLocation,
        ownerId: user.uid,
        businessType,
        createdAt: serverTimestamp(),
      };

      let collectionName = '';
      let roleValue = '';

      switch (businessType) {
        case 'rental_company':
          collectionName = 'rentalCompanies';
          roleValue = 'company_owner';
          break;
        case 'private_provider':
          collectionName = 'privateProviders';
          roleValue = 'private_owner';
          break;
        case 'agent':
          collectionName = 'agents';
          roleValue = 'agent';
          break;
      }

      const docRef = await addDoc(collection(db, collectionName), data);

      await updateDoc(doc(db, 'users', user.uid), {
        role: roleValue,
        companyId: docRef.id,
        updatedAt: serverTimestamp(),
      });

      // ‚úÖ G·ªçi Cloud Function ƒë·ªÉ set custom claims
      if (businessType === 'rental_company') {
        await fetch('/api/setCustomClaims', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ uid: user.uid, role: 'company_owner' }),
        });
        await auth.currentUser?.getIdToken(true); // refresh token ƒë·ªÉ nh·∫≠n custom claim m·ªõi        
      }
        // üîÅ ƒê·ª£i 1.5s tr∆∞·ªõc khi refresh token
        await new Promise((resolve) => setTimeout(resolve, 1500));

        // üîÑ Refresh token ƒë·ªÉ nh·∫≠n role m·ªõi
        await auth.currentUser?.getIdToken(true);

      alert('‚úÖ Company created!');
      router.push(
        businessType === 'rental_company'
          ? '/my-business/stations'
          : '/my-business'
      );
    } catch (err) {
      console.error('‚ùå Error:', err);
      alert('‚ùå Failed to create company.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-4">
      <Input placeholder="Company Name" value={name} onChange={(e) => setName(e.target.value)} />
      <Input placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)} />
      <Input placeholder="Phone" value={phone} onChange={(e) => setPhone(e.target.value)} />
      <Input
        placeholder="Display Address (for users)"
        value={displayAddress}
        onChange={(e) => setDisplayAddress(e.target.value)}
      />
      <Input
        placeholder="Map Address (Google Maps link)"
        value={mapAddress}
        onChange={(e) => setMapAddress(e.target.value)}
        onBlur={handleBlur}
      />
      <Input
        placeholder="Location (lat,lng)"
        value={location}
        readOnly={!!coords}
        onChange={(e) => setLocation(e.target.value)}
      />

      {geoLoading && <p className="text-sm text-gray-500">üìç Detecting coordinates...</p>}
      {geoError && <p className="text-sm text-red-500">{geoError}</p>}
      {coords && (
        <>
          <p className="text-sm text-gray-600">
            üìå Detected: {coords.lat}¬∞ N, {coords.lng}¬∞ E
          </p>
          <iframe
            title="Map Preview"
            width="100%"
            height="200"
            style={{ border: 0, borderRadius: '8px' }}
            loading="lazy"
            allowFullScreen
            src={`https://www.google.com/maps?q=${coords.lat},${coords.lng}&hl=vi&z=16&output=embed`}
          ></iframe>
        </>
      )}

      <Button onClick={handleSubmit} disabled={loading}>
        {loading ? 'Creating...' : 'Add Company'}
      </Button>
    </div>
  );
}
